/* Container for all product cards */
.product-container {
    display: flex;
    justify-content: center;
    padding: 20px;
  }
  
  /* Individual product card */
  .product-card {
    background: white;
    border-radius: 10px;
    box-shadow: 0 4px 6px rgba(11, 109, 238, 0.466);
    padding: 15px;
    width: 250px;
    text-align: center;
    position: relative;
  }
  
  /* Discount tag */
  .product-discount {
    position: absolute;
    top: 10px;
    left: 10px;
    background: red;
    color: white;
    font-size: 12px;
    font-weight: bold;
    padding: 5px 10px;
    border-radius: 5px;
  }
  
  /* Product image */
  .product-image {
    width: 100%;
    height: 200px;
    object-fit: cover;
    border-radius: 8px;
  }
  
  /* Product title */
  .product-title {
    font-size: 16px;
    font-weight: bold;
    margin: 10px 0;
  }
  
  /* Price styling */
  .product-price {
    display: flex;
    justify-content: center;
    gap: 10px;
    margin-bottom: 10px;
  }
  
  .current-price {
    color: #e74c3c;
    font-weight: bold;
  }
  
  .old-price {
    text-decoration: line-through;
    color: gray;
    font-size: 14px;
  }
  
  /* Product options: colors and sizes */
  .product-options {
    display: flex;
    justify-content: space-between;
    margin: 10px 0;
  }
  
  .product-colors {
    display: flex;
    gap: 5px;
  }
  
  .color-circle {
    width: 15px;
    height: 15px;
    border-radius: 50%;
    border: 1px solid #ccc;
  }
  
  .product-sizes {
    display: flex;
    gap: 5px;
  }
  
  .size-option {
    background: #f0f0f0;
    border-radius: 5px;
    padding: 2px 6px;
    font-size: 12px;
  }
  
  /* Product rating */
  .product-rating {
    margin: 10px 0;
    color: #f39c12;
  }
  
  /* Buy button */
  .product-buy-btn {
    background: #2ecc71;
    color: white;
    border: none;
    border-radius: 5px;
    padding: 10px 15px;
    font-size: 14px;
    cursor: pointer;
    margin-bottom: 10px;
  }
  
  .product-buy-btn:hover {
    background: #27ae60;
  }
  
  /* Offer section */
  .product-offer {
    background: #8e44ad;
    color: white;
    padding: 10px;
    border-radius: 5px;
    margin-top: 10px;
    font-size: 14px;
  }
  
  .product-offer span {
    font-weight: bold;
    margin-top: 5px;
    display: block;
  }
  